import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { supabase } from './lib/supabase';
import Login from './components/Login';
import Vehicles from './components/Vehicles';
import SPVSetup from './components/SPVSetup';
import DealPage from './components/DealPage';
import CreateUser from './components/CreateUser';
import Unauthorized from './components/Unauthorized';

function App() {
  const [session, setSession] = useState(null);
  const [isAdmin, setIsAdmin] = useState(false);

  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session);
      checkAdminStatus(session?.user?.email);
    });

    const {
      data: { subscription },
    } = supabase.auth.onAuthStateChange((_event, session) => {
      setSession(session);
      checkAdminStatus(session?.user?.email);
    });

    return () => subscription.unsubscribe();
  }, []);

  const checkAdminStatus = async (email) => {
    if (!email) {
      setIsAdmin(false);
      return;
    }

    if (email === 'admin@twelled.com') {
      setIsAdmin(true);
    } else {
      setIsAdmin(false);
    }
  };

  return (
    <Router basename="/eval-task-spv">
      <Routes>
        <Route path="/login" element={<Login />} />
        <Route path="/unauthorized" element={<Unauthorized />} />
        <Route
          path="/"
          element={
            session ? (
              <Navigate to="/vehicles" replace />
            ) : (
              <Navigate to="/login" replace />
            )
          }
        />
        <Route
          path="/vehicles"
          element={
            session ? <Vehicles isAdmin={isAdmin} /> : <Navigate to="/login" />
          }
        />
        <Route
          path="/spv-setup"
          element={session ? <SPVSetup /> : <Navigate to="/login" />}
        />
        <Route
          path="/deal/:spvId"
          element={session ? <DealPage /> : <Navigate to="/login" />}
        />
        <Route
          path="/create-user"
          element={
            session && isAdmin ? (
              <CreateUser />
            ) : (
              <Navigate to="/unauthorized" />
            )
          }
        />
      </Routes>
    </Router>
  );
}

export default App;
